    transition: 300ms; }

.btn-principal {
  background-color: #f84756; }
  .btn-principal a {
    display: block;
    color: #fef1f1;
    text-align: center;
    line-height: 5rem; }

/*	#################################
                         Layout
                #################################*/
.contenedor {
  width: 90%;
  min-height: 50rem;
  margin: 10rem auto;
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  align-items: center; }
  @media (min-width: 768px) {
    .contenedor {
      height: 56rem;
      margin: 10 auto; } }
  @media (min-width: 1200px) {
    .contenedor {
      height: 70rem; } }

.avatar {
  min-width: 20rem;
  min-height: 20rem;
  border: 8px solid #12141e;
  border-radius: 50%;
  position: relative;
  z-index: 100;
  overflow: hidden; }
  @media (min-width: 768px) {
    .avatar {
      width: 40rem;
      height: 40rem; } }
  .avatar:hover .avatar__imagen:nth-child(2) {
    opacity: 0;
    transition: 300ms; }
  .avatar:active .avatar__imagen:nth-child(2) {
    opacity: 0;
    transition: 300ms; }
  .avatar .avatar__imagen {
    position: absolute;
    width: 100%; }

.misDatos .nombre {
  font-size: 3rem;
  font-weight: 700;
  color: #fef1f1; }

.misDatos .carrera {
  font-size: 5rem;
  color: #fef1f1;
  font-weight: bolder; }

/*	#################################
                         Boton superior izquierdo
                #################################*/
.menu {
  width: 7rem;
  height: 7rem;
  border-radius: 50%;
  background-color: #f84756;
  position: absolute;
  top: 1rem;
  left: 1rem;
  z-index: 150;
  cursor: pointer;
  display: flex;
  justify-content: center;
  align-items: center;
  transition: 300ms; }
  .menu:hover {
    transition: 300ms;
    transform: scale(1.2); }

.menu-barras {
  flex-direction: column; }
  .menu-barras .menu__barra {
    width: 5rem;
    height: 1rem;
    background-color: white;
    border-radius: 3rem;
    margin-top: 0.3rem;
    transition: 300ms; }
  .menu-barras .menu__barra:first-of-type {
    margin-top: none; }

.menu-cruz .menu__barra {
  width: 5rem;
  height: 1rem;
  background-color: white;
  border-radius: 3rem;
  position: absolute;
  transition: 300ms;
  transform: rotate(30deg); }

.menu-cruz .menu__barra:last-of-type {
  transform: rotate(-30deg); }

/*	#################################
                         Opciones de menu
                #################################*/
.opciones {
  width: 100%;
  height: 100%;
  min-height: 640px;
  background-color: #f84756;
  position: absolute;
  top: 0;
  left: 0;
  z-index: 140;
  display: flex;
  align-items: center;
  justify-content: center; }
  .opciones .opciones__wrap {
    width: 45rem;
    height: 30rem;
    display: flex;
    flex-direction: column;
    justify-content: space-between; }
    .opciones .opciones__wrap a {
      text-align: center;
      display: block;
      font-size: 5rem;
      color: #fef1f1;
      font-weight: bolder; }
      .opciones .opciones__wrap a:hover {
        transform: rotate(-10deg);
        transition: 300ms; }

.ocultarMenu {
  max-width: 0;
  overflow: hidden;
  transition: 300ms; }

.mostrarMenu {
  max-width: 100%;
  overflow: visible;
  transition: 300ms; }

.sobre-mi {
  width: 90%;
  min-height: 30rem;
  padding-top: 5rem;
  margin: 0 auto;
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  align-items: center;
  position: relative; }
  @media (min-width: 768px) {
    .sobre-mi .avatar {
      width: 20rem;
      height: 20rem; } }
  .sobre-mi .misDatos {
    text-align: center; }
