  clip-path: polygon(0% 0%, 100% 0%, 100% 90%, 0% 100%); }

.contenido {
  display: flex;
  flex-direction: column;
  min-height: 40rem;
  padding-top: 3rem;
  align-items: center; }
  .contenido h2 {
