.gitbutton {
  background-color: #141111;
  margin-right: 1rem; }

.wrap__proyectos {
  width: 100%;
  height: 100%;
  position: absolute; }
  .wrap__proyectos .boton {
    box-sizing: border-box;
    padding-top: 0.2rem;
    display: flex;
    align-items: center;
    justify-content: center; }
  .wrap__proyectos .proyectos {
